:root{
    --color : #6b327e;
}
html{
    font-size: 62.5%;
}

body{
    font-size: 1.6rem;
    font-family: $font;
    overflow: hidden;
}

strong{
    font-weight: bold;
}

.container{
    width: 100%;
    max-width: 111rem;
    margin: 0 auto;
    text-align: center;
    height: calc(100% - 9.8rem);
    display: flex;
    flex-direction: column;
}

h1,h2,h3{
    font-weight: bold;
    color: var(--color);
    text-shadow: 0px 4px 2px rgba(0,0,0,0.3);
}

h1{
    font-size: 4.2rem;
    text-transform: uppercase;
    
}
h2{
    font-size: 3.8rem;
    margin-bottom: 2rem;
}

h3{
    font-size: 2.8rem;
}

img{
    max-width: 100%;
}

.header{
    padding: 2rem;
    &__logo{
        width: 15rem;
        display: block;
    }
}

.footer{
    text-align: right;
    color: var(--color);
    text-shadow: 0px 2px 2px rgba(0,0,0,0.5);
    margin-top: auto;
    margin-bottom: 2rem;
}

.login{
    margin-top: 3rem;
    &__img{
        margin: 4rem 0;
    }
    &__title{
        color: var(--color);
        margin-bottom: 2rem;
    }
}

form{
    text-align: left;
    label{
        color: var(--color);
        width: 9rem;
        text-align: left;
        display: block;
        position: relative;
        &:after{
            content: ":";
            position: absolute;
            right: 0;
            font-weight: bold;
        }
        &.select{
            width: 17rem;
            font-size: 1.8rem;
        }
    }
    input[type="text"],input[type="password"]{
        border: 3px solid var(--color);
        margin-left: 2rem;
        padding: 0.5rem;
        &#organization{
            width: 50%;
        }
    }
    button,.button{
        background-color: var(--color);
        color: #fff;
        padding: 1rem 4rem;
        display: block;
        margin: 0 auto;
        border: none;
    }
    .button{
        display: inline-block;
        text-decoration: none;
        font-size: 1.4rem;
    }
    select{
        padding: 0.7rem 0.5rem;
        border: 3px solid var(--color);
        width: 30rem;
        margin: 0 2rem;
    }
    .form{
        &__row{
            display: flex;
            justify-content: center;
            margin-bottom: 2rem;
            align-items: center;
        }
    }
}
.list{
    border: 1px solid #000;
    width: 62rem;
    margin: 2rem auto 0;
    text-align: left;
    li{
        border-bottom: 1px solid #000;
        display: flex;
        padding: 1rem 2rem;
        background-color: #e4e4e4;
        color: var(--color);
        &:last-child{
            border-bottom: none;
        }
    }
    &__name{
        width: 60%;
        color: #000;
        strong{
            color: var(--color);
        }
    }
    &__action{
        width: 40%;
        text-align: right;
    }
    a{
        margin: 1rem;
    }
}
.toolbar{
    border: 1px solid #000;
    display: flex;
    align-items: center;
    padding: 2rem;
    background-color: #e4e4e4;
    font-size: 1.8rem;
    box-shadow: 0 3px 2px rgba(0,0,0,0.2);
    margin: 6rem 0;
    &__title{
        color: var(--color);
    }
    &__nav{
        display: flex;
        margin-left: auto;
    }
    &__exit{
        margin-left: 1rem;
        a{
            color: var(--color);
        }
    }
}

.action{
    padding: 0 24.5rem;
    font-size: 1.8rem;
    margin-top: 2rem;
    color: var(--color);
    &__row{
        width: 100%;
        text-align: left;
        margin-bottom: 2rem;
    }
}
